.card_wrapper {
  height: 100%;
  .cart_container {
    height: auto;
    margin: 8rem 0;
  
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2rem;
  
    padding: 2rem;
  
    text-align: left;
    box-shadow: none;

    h2 {
      font-size: 28px;
    }
  
    .card_btn {
      background-color: rgb(188, 110, 126);
      color: #fff;
      font-weight: 700;
  
      letter-spacing: 2px;
      font-size: 10px;
      border: none;
      border-radius: 20px;
      padding: 20px;
  
      p {
        font-size: 12px;
      }
    }
  
    .cart_total {
      h2 {
        font-size: 16px;
        color: #9ca3af;
      }
  
      p {
        font-size: 18px;
        font-weight: 600;
      }
  
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
  }
}

@media screen and (min-width: 800px) {
  .card_wrapper {
    height: 100vh;
    .cart_container {
      margin: 10rem auto;
      
      width: 40%;
      
      .item_container {
        max-height: 500px;
        overflow-y: scroll;

          .card_btn {
          margin: 0 auto;
          cursor: pointer;
          background-color: #efefef;
          color: #000;
          width: 30%;
          
          &:hover {
            background-color: rgb(188, 110, 126);
            color: #fff;
          transition: 0.5s all ease;
        }
      }
      }
      .item_container::-webkit-scrollbar {
    display: none;
    }
    }

  }
}
